import {Component, Input, ContentChildren, QueryList, forwardRef} from '@angular/core';
import {SBItem} from './sb-item';


export class SqueezeBox {
    
     multiple: boolean = true
    
     items: QueryList<SBItem>;
    
    constructor() {}
    
    didItemToggled(item: SBItem) {
        // on not multiple, it will collpase the rest of items
        if (!this.multiple) {
            this.items.toArray().forEach(function(i) { 
                if (i !== item) {
                    i.applyToggle(true)
                }
            });
        }
    }

static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
    exportAs: 'squeezebox',
    selector: 'squeezebox',
    template: `
        <div class="accordion">
            <ng-content></ng-content>
        </div>
    `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'multiple': [{ type: Input },],
'items': [{ type: ContentChildren, args: [forwardRef(() => SBItem), ] },],
};
}
interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
